                  v-model="newRenterPhone"
                  @focusout="getUser(newRenterPhone, 'newRenter')"
                />
              </div>
            </b-form-group>
            <b-form-group>
              <div class="d-flex justify-content-between">
                <label class="m-auto w-50">نام مستاجر جدید</label>
                <b-form-input v-model="newRenterName"/>
              </div>
            </b-form-group>
          </div>
          <div v-else>
            <b-form-group label-for="select1">
              <custom-vue-select
                id="select1"
                v-model="selectedRenter"
                :options="users"
                based-on="key"
                target-key="value"
              ></custom-vue-select>
            </b-form-group>
          </div>
          <b-form-group class="mt-1" label="تاریخ ورود مستاجر جدید">
