    </b-modal>
    <b-modal
      id="modal-old-owner"
      size="sm"
      hide-footer
      hide-header-close
      centered
      :title="chosenUnit?  chosenUnit.floor.floor_name+' / '+ chosenUnit.unit_name: ''"
    >
      <b-form @submit.prevent="oldOwnerRegister">
        <b-form-group label-for="cost-date" label="تاریخ خروج مالک">
          <div class="d-flex align-items-center">
            <input type="text" class="custom-input"/>
            <date-picker
              v-model="ownerExitDate"
              color="#7468F0"
              auto-submit
              custom-input=".custom-input"
            />
          </div>
        </b-form-group>
        <b-form-group>
          <b-form-radio class="mb-1" v-model="owner" name="owner" value="new">
            ثبت مالک
          </b-form-radio>
          <b-form-radio v-model="owner" name="owner" value="current">
            انتخاب از اهالی ساختمان
          </b-form-radio>
        </b-form-group>
        <template v-if="owner === 'new'">
          <b-form-group>
            <div class="d-flex justify-content-between">
              <label class="m-auto w-50">تلفن همراه مالک</label>
              <b-form-input
                maxlength="11"
                v-model="newOwnerPhone"
                @focusout="getUser(newOwnerPhone, 'newOwner')"
              />
            </div>
          </b-form-group>
          <b-form-group>
            <div class="d-flex justify-content-between">
              <label class="m-auto w-50">نام مالک جدید</label>
              <b-form-input v-model="newOwnerName"/>
            </div>
          </b-form-group>
        </template>
        <template v-else>
          <b-form-group label-for="select2">
            <custom-vue-select
              id="select2"
              v-model="selectedOldOwner"
              :options="users"
              based-on="key"
              target-key="value"
            ></custom-vue-select>
          </b-form-group>
        </template>
        <div class="text-center mt-3 d-flex justify-content-between">
          <b-button
            variant="primary"
            class="mr-1 w-50"
            @click="oldOwnerRegister"
          >
            ثبت
          </b-button>
          <b-button
            variant="outline-secondary"
            class="w-50"
            @click="$bvModal.hide('modal-old-owner')"
          >
            انصراف
          </b-button>
        </div>
      </b-form>
    </b-modal>
    <b-modal
      id="modal-new-renter"
      size="sm"
      hide-footer
      hide-header-close
      centered
      :title="chosenUnit?  chosenUnit.floor.floor_name+' / '+ chosenUnit.unit_name: ''"
    >
      <b-form  @submit.prevent="newRenterRegister">
        <b-form-group>
          <b-form-radio class="mb-1" v-model="renter" name="renter" value="new">
            ثبت مستاجر جدید
          </b-form-radio>
          <b-form-radio v-model="renter" name="renter" value="current">
            انتخاب از اهالی ساختمان
          </b-form-radio>
        </b-form-group>
        <div v-show="renter === 'new'">
          <b-form-group>
            <div class="d-flex justify-content-between">
              <label class="m-auto w-50">تلفن همراه مستاجر</label>
              <b-form-input
                maxlength="11"
                v-model="newResidentPhone"
                @focusout="getUser(newResidentPhone, 'newResident')"
              />
            </div>
          </b-form-group>
          <b-form-group>
            <div class="d-flex justify-content-between">
              <label class="m-auto w-50">نام مستاجر جدید</label>
              <b-form-input v-model="newResidentName"/>
            </div>
          </b-form-group>
        </div>
        <div v-show="renter !== 'new'">
          <b-form-group label-for="select3">
            <custom-vue-select
              id="select3"
              v-model="selectedNewResident"
              :options="users"
              based-on="key"
              target-key="value"
            ></custom-vue-select>
          </b-form-group>
        </div>
        <b-form-group label-for="cost-date" label="تاریخ ورود مستاجر جدید">
          <div class="d-flex align-items-center">
            <input type="text" class="custom-input"/>
            <date-picker
              v-model="renterEnterDate"
              color="#7468F0"
              auto-submit
              custom-input=".custom-input"
            />
          </div>
        </b-form-group>
        <div class="text-center mt-3 d-flex justify-content-between">
          <b-button
            variant="primary"
            class="mr-1 w-50"
            @click="newRenterRegister"
          >
            ثبت
          </b-button>
          <b-button
            variant="outline-secondary"
            class="w-50"
            @click="$bvModal.hide('modal-new-renter')"
          >انصراف
          </b-button>
        </div>
      </b-form>
    </b-modal>
    <b-modal
      id="modal-meterage"
      size="sm"
      hide-footer
      hide-header-close
      centered
      :no-close-on-backdrop="true"
      :title="chosenUnit?  chosenUnit.floor.floor_name+' / '+ chosenUnit.unit_name: ''"
    >
      <b-form
        class="ownerForm-form"
        @submit.prevent
      >
        <b-form-group>
          <div class="d-flex">
            <label class="my-auto mr-1" style="width: 40%">متراژ </label>
            <b-form-input
              v-model="footage"
              id="unit-meterage"
              name="unit-meterage"
              style="text-align: left; font-weight: 300; letter-spacing: 0.1em; font-size: 120%; direction: ltr; padding-top: 10px;"
            />
          </div>
        </b-form-group>
        <b-form-group>
          <div class="d-flex">
            <label class="my-auto mr-1" style="width: 40%">تعداد ساکنین</label>
            <b-form-input
              v-model="residentCount"
              id="unit-residents"
              name="unit-residents"
              style="text-align: left;font-weight: 300; letter-spacing: 0.1em; font-size: 120%; direction: ltr; padding-top: 10px;"
            />
          </div>
        </b-form-group>
      </b-form>
      <div class="text-center">
        <b-button @click="meterageRegister" variant="primary" style="width: 40%" class="mr-1">ثبت</b-button>
        <b-button @click="$bvModal.hide('modal-meterage')" variant="outline-secondary" style="width: 40%">انصراف
        </b-button>
      </div>
    </b-modal>
  </div>
</template>

<script>
import VuePersianDatetimePicker from "vue-persian-datetime-picker";
import CustomVueSelect from "@/@core/components/custom-vue-select/CustomVueSelect.vue";
import managerService from "@/api/managerService";
import AppCollapse from "@core/components/app-collapse/AppCollapse.vue";
import AppCollapseItem from "@core/components/app-collapse/AppCollapseItem.vue";
import {
    BOverlay,
    BButton,
    BAvatar,
    BCard,
    BCardBody,
    BCardTitle,
    BCol,
    BForm,
    BFormGroup,
    BFormRow,
    BFormRadio,
    BFormRadioGroup,
    BFormInput,
    BImg,
    BInputGroup,
    BModal,
    BRow,
    BCollapse,
} from "bootstrap-vue";
import axios from "@/libs/axios";
import ToastificationContent from "@core/components/toastification/ToastificationContent.vue";
import BookIcon from "@core/components/svg-icons/BookIcon.vue";

export default {
    name: "Registration",
    components: {
        AppCollapse,
        AppCollapseItem,
        CustomVueSelect,
        BookIcon,
        BOverlay,
        BButton,
        BAvatar,
        BCard,
        BCardBody,
        BCardTitle,
        BCol,
        BForm,
        BFormGroup,
        BFormRow,
        BFormRadio,
        BFormRadioGroup,
        BFormInput,
        BImg,
        BInputGroup,
        BModal,
        BRow,
        BCollapse,
        datePicker: VuePersianDatetimePicker,
    },
    data() {
        return {
            floors: null,
            visible: false,
            collapseStates: {},
            showLoading: false,
            selectedUnit: null,
            selectedNewResident: {},
            selectedOldOwner: {},
            selectedRenter: {},
            renterEnterDate: "",
            renterExitDate: "",
            newRenterName: "",
            newRenterPhone: "",
            newOwnerName: "",
            newOwnerPhone: "",
            newResidentName: "",
            newResidentPhone: "",
            users: [],
            renter: "new",
            renterOptions: [
                {
                    text: "ثبت مستاجر جدید",
                    value: "new",
                },
                {
                    text: "انتخاب از اهالی ساختمان",
                    value: "current",
                },
            ],
            owner: "new",
            ownerOptions: [
                {
                    text: "ثبت مالک",
                    value: "new",
                },
                {
                    text: "انتخاب از اهالی ساختمان",
                    value: "current",
                },
            ],
            selectedradio: "no",
            optionsradio: [
                {
                    text: "خیر",
                    value: "no",
                },
                {
                    text: "بله",
                    value: "yes",
                },
            ],
            ownerExitDate: "",
            usersData: [],
            selectedUnitUser: null,
            userDetail: null,
            chosenUnit: null,
            footage: '',
            residentCount: ''
        };
    },
    created() {
        this.getUnits();
        this.getUsers();
    },
    methods: {
        setModal(unit, modal, unitUser) {
            this.selectedNewResident = null;
            this.selectedOldOwner = null;
            this.selectedRenter = null;
            this.renterEnterDate = null;
            this.renterExitDate = null;
            this.newRenterName = null;
            this.newRenterPhone = null;
            this.newOwnerName = null;
            this.newOwnerPhone = null;
            this.newResidentName = null;
            this.newResidentPhone = null;
            this.ownerExitDate = null;
            this.selectedUnit = unit;
            this.selectedUnitUser = unitUser;
            this.chosenUnit = unit
            this.$bvModal.show(modal);
        },
        async getUsers() {
            const { data } = await managerService.user();
            this.usersData = data.list;
            data.list.forEach((user) => {
                this.users.push({
                    key: user.name,
                    value: user.user_id,
                });
            });
        },
        async getUnits() {
            const { data } = await managerService.unit();
            data.list.forEach((unit) => {
                unit.show = false;
            });
            this.floors = data.list.reduce((groups, item) => {
                const group = groups[item.floor.floor_name] || [];
                group.push(item);
                groups[item.floor.floor_name] = group;
                return groups;
            }, {});
        },
        getDetail(unit, floor) {
            if (unit.show) {
                unit.show = false;
            } else {
                if (unit.detail) {
                    unit.show = true;
                    return
                }
                const params = { unit_id: unit.unit_id };
                managerService.unitHistory(params)
                    .then((response) => {
                        this.floors[floor].forEach((singleUnit) => {
                            if (singleUnit.unit_id === unit.unit_id) {
                                singleUnit.detail = response.data.list;
                                unit.show = true;
                                unit.showCollapse = false
                            }
                        });
                    });
            }
        },
        deleteUser(userType, unit) {
            const params = {
                user_unit_id:
                    userType === "1"
                        ? unit.owner?.user_unit_id
                        : unit.renter?.user_unit_id,
            };
            managerService.userUnitDelete(params);
        },
        async getUser(phone, modal) {
            const params = { mobile: phone };
            const { data } = await managerService.userDetail(params);
            this.userDetail = data?.data;
            switch (modal) {
                case "newRenter":
                    this.newRenterName = data?.data?.full_name;
                    break;
                case "newResident":
                    this.newResidentName = data?.data?.full_name;
                    break;
                case "newOwner":
                    this.newOwnerName = data?.data?.full_name;
                    break;
            }
        },
        setModalInfo(modalType, unit) {
            this.footage = unit.footage
            this.residentCount = unit.resident_count
            this.chosenUnit = unit
        },
        oldRenterRegister() {
            managerService.userUnitStore({
                user_unit_id: this.selectedUnitUser.user_unit_id,
                start_date: this.renterEnterDate,
                end_date: this.renterExitDate,
                name: this.newRenterName,
                mobile: this.newRenterPhone,
                user_id: this.selectedRenter?.value?.value,
            })
                .then(res => {
                    this.$bvModal.hide('modal-old-renter')
                    this.getUnits()
                    this.selectedUnit.showCollapse = false
                })
        },
        newRenterRegister() {
            managerService.userUnitStore({
                start_date: this.renterEnterDate,
                name: this.newResidentName,
                mobile: this.newResidentPhone,
                unit_id: this.selectedUnit.unit_id,
                user_id: this.selectedNewResident?.value?.value,
            })
                .then(res => {
                    this.$bvModal.hide('modal-new-renter')
                    this.getUnits()
                    this.selectedUnit.showCollapse = false
                })
        },
        oldOwnerRegister() {
            managerService.userUnitStore({
                user_unit_id: this.selectedUnitUser.user_unit_id,
                start_date: this.renterEnterDate,
                end_date: this.ownerExitDate,
                name: this.newOwnerName,
                mobile: this.newOwnerPhone,
                user_id: this.selectedOldOwner?.value?.value,
            })
                .then(res => {
                    this.$bvModal.hide('modal-old-owner')
                    this.getUnits()
                    this.selectedUnit.showCollapse = false
                })
        },
        meterageRegister() {
            managerService.unitStore({
                unit: [{
                    unit_id: this.chosenUnit.unit_id,
                    footage: this.footage,
                    resident_count: this.residentCount
                }]
            })
                .then(res => {
                    this.$bvModal.hide('modal-meterage')
                    this.getUnits()
                })
        },
    },
};
</script>

