    width: 100%;
    border: none;
    background: none;
    font-size: 16px;
    color: #7367f0;
}

.custom-input,
.custom-input-nd {
    border: 1px solid #d8d6de;
    border-radius: 5px;
    padding: 10px 5px !important;
